import common from '../../components/common.vue'
export default {
	data() {
		return {
			datalist:[]
		};
	},
	components:{
		common
	},
	computed:{
		computedTotal(){
			// 依赖发生改变，计算属性会重新计算一遍
			var sum = 0;
			this.datalist.forEach(item=>{
				if(item.checked){
					sum+= item.number*item.good.price
				}
			})
			
			return sum
		},
		isAllChecked(){
			return this.datalist.every(item=>item.checked===true)
		}
	},
	onShow(){
		uni.request({
			url:"http://localhost:3000/carts?_expand=good",
			success: (res) => {
				console.log(res.data)
				this.datalist =res.data
			}
		})
	},
	methods:{
		total(){
			var sum = 0;
			this.datalist.forEach(item=>{
				if(item.checked){
					sum+= item.number*item.good.price
				}
			})
			
			return sum
		},
		
		handleChecked(index){
			console.log(index)
			this.datalist[index].checked = !this.datalist[index].checked
			
			//update更新接口
			this.update(this.datalist[index])
		},
		handleMinus(index){
			this.datalist[index].number--
			if(this.datalist[index].number===0){
				this.datalist[index].number = 1
			}
			this.update(this.datalist[index])
		},
		handleAdd(index){
			this.datalist[index].number++
			this.update(this.datalist[index])
		},
		update(obj){
			let {id,goodId,checked,username,number} = obj
			uni.request({
				url:`http://localhost:3000/carts/${obj.id}`,
				method:"PUT",
				data:{
					 goodId,
					 id,
					 checked,
					 username,
					 number
				}
			})
		},
		handlePress(id){
			uni.showModal({
				title:"提示",
				content:"你确定要删除?",
				success: (res) => {
					console.log(res)
					if(res.confirm){
						this.deleteCart(id)
					}
				}
			})
		},
		deleteCart(id){
			this.datalist = this.datalist.filter(item=>item.id!==id)
			uni.request({
				url:`http://localhost:3000/carts/${id}`,
				method:"DELETE",
				success: () => {
					
				}
			})
		},
		
		handldeAllChecked(ev){
			console.log(ev.detail.value)
			if(ev.detail.value.length===0){
				this.datalist = this.datalist.map(item=>({
					...item,
					checked:false
				}))
			}else{
				this.datalist = this.datalist.map(item=>({
					...item,
					checked:true
				}))
			}
		}
	}
}
